import sendAPI from './Network';
import { getStorage } from './Storage';

/*************** MASTER START ***************/
const makeAdmin = async ({
    id,
    nickname,
    password
}) => {
    try {
        const res = await sendAPI({
            url: '/master/user',
            method: 'post',
            data: {
                id,
                nickname,
                password,
                master_id: getStorage({ key: 'user_info' }).nickname
            }
        });

        return res.data;
    } catch (err) {
        alert('    ');
        throw err;
    }
};

